<template>
  <div id="app">
    <mainIndex/>
  </div>
</template>

<script>
import mainIndex from './components/main'

export default {
  name: 'app',
  components: {
    mainIndex
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  background: -webkit-radial-gradient(circle closest-side,rgb(0,97,102) 0%, rgb(6,72,83) 47%, rgb(12,47,64) 100%); /* Safari 5.1 - 6.0 */
    background: -o-radial-gradient(circle closest-side,rgb(0,97,102) 0%, rgb(6,72,83) 47%, rgb(12,47,64) 100%); /* Opera 11.6 - 12.0 */
    background: -moz-radial-gradient(circle closest-side,rgb(0,97,102) 0%, rgb(6,72,83) 47%, rgb(12,47,64) 100%);/* Firefox 3.6 - 15 */
    background: radial-gradient(circle closest-side,rgb(0,97,102) 0%, rgb(6,72,83) 47%, rgb(12,47,64) 100%);/* 标准的语法 */
}
html,body,#app{
  width: 100%;
  height: 100%;
  margin:0;
}
</style>



