<template>
  <div style="width: 100%;height:100%;" @click="listHide">
    <div id="map"></div>
    <div v-show = "listShow" id="list" :style='"left:" + listStyle.left + "px;top:" + listStyle.top + "px;"'>
      <div v-for="ele in list"><a @click="listCli(ele.href)">{{ ele.title }}</a></div>
    </div>
    <div id="button">
      <div v-for="ele,index in button"><a :class="ele.selected?'selected':''" @click="buttonCli(ele.key,index)">{{ ele.value }}</a></div>
    </div>
    <div v-show = "surveyShow" id="show">
      <div class="showTitle">工程概况</div>
      <table style="width: 300px">
        <tr>
          <td style="text-align: right">长度：</td>
          <td style="text-align: left">{{ survey.length }}</td>
        </tr>
        <tr>
          <td style="text-align: right">额定电压：</td>
          <td style="text-align: left">{{ survey.voltage }}</td>
        </tr>
        <tr>
          <td style="text-align: right">额定功率：</td>
          <td style="text-align: left">{{ survey.power }}</td>
        </tr>
        <tr>
          <td style="text-align: right">投产时间：</td>
          <td style="text-align: left">{{ survey.time }}</td>
        </tr>
      </table>
    </div>
    <div v-show = "devInfoShow" id="show">
      <div class="showTitle">设备信息</div>
      <br />
      <br />
      <br />
      <br />
      <br />
    </div>
  </div>
</template>
<script>
  export default {
    data(){
      return{
        mapJson:{},
        chart:{},
        button:[
          {
            key: "move",
            selected: false,
            value: "拖动"
          }
        ],
        survey:{
          length: "2090",
          voltage: "800kV",
          power: "720万KW",
          time: "2012年12月12日"
        },
        roam: true,
        surveyShow: false,
        devInfoShow: false,
        buttonTemp:[],
        moveSubmit:false,
        moveReady:false,
        pointMove: {
          state:false,
          dataIndex:[-1,0]
        },
        pointMoved: [],
        title:"",
        listShow: false,
        listStyle: {
          left:0,
          top:0
        },
        key:"",
        list:[],
        listData:{
          point: [],
          line: []
        },
        dataTemp:[],
        data:[]
      }
    },
    methods:{
      buttonCli(key,index){
        this.pointMoveStateToFalse()
        this.selectedClear(index)
        this.button[index].selected = !this.button[index].selected
        if(key === "move"){
          this.roam = this.moveSubmit 
          this.moveSubmit = !this.moveSubmit
          this.mapDraw()
          this.dataTemp = JSON.parse(JSON.stringify(this.data))
          this.buttonTemp = JSON.parse(JSON.stringify(this.button))
        }else if(key === "moveSubmit"){
          this.moveReady = false
          var params = new URLSearchParams()
          params.append("point", this.pointMoved)
          this.$Axios.post('/setPointCoord', params).then(res => {
            console.log(this.pointMoved)
            this.pointMoved = []
          }).catch( 
            err => console.log(err)
          );
          this.button = this.buttonTemp
          this.selectedClear(-1)
          this.roam = true
          this.mapDraw()
        }else if(key === "moveCancel"){
          this.pointMoved = []
          this.moveReady = false
          this.data = this.dataTemp
          this.button = this.buttonTemp
          this.selectedClear(-1)
          this.roam = true
          this.mapDraw()
        }
      },
      selectedClear(index){
        let temp = this.button.map((ele,i) => {
          if(index != i){
            ele.selected = false
          }
          return ele
        })
        this.button = temp
      },
      selectCoordById(id,data) {
        for(let i = 0; i < data.length; i++){
              if(data[i].id == id){
                return data[i].coord
              }
        }
        console.error(data,"找不到id为" + id + "的点")
        return []
      },
      listHide() {
        this.listShow = false
        this.surveyShow = false
        this.devInfoShow = false
      },
      formatLegend(data) {
        let legend = []
        data.forEach(ele =>{
          legend.push({
            name: ele.legend
          })
        })
        return legend
      },
      listCli(param) {
        this.listHide()
        if(param === "survey"){
          this.$nextTick(function () {
            this.surveyShow = true
          })
        }else if(param === "devInfo"){
          this.$nextTick(function () {
            this.devInfoShow = true
          })
        }else{
          window.open(param + "?id=" + this.key)
        }
      },
      formatSeries(data) {
        let series = []
        data.forEach(ele =>{
          let lineData = []
          let pointData = []
          if(ele.point && ele.point.length > 0){
            ele.point.forEach(point => {
              pointData.push({
                name: point.name,
                symbolSize:10,
                label: {
                  normal: {
                    show: false
                  },
                  emphasis: {
                    show: false
                  }
                },
                itemStyle: {
                  normal: {
                    color: ""
                  }
                },
                coord: point.coord
              })
            })
          }
          if(ele.line && ele.line.length > 0){
            ele.line.forEach(line => {
              lineData.push(
                [
                  {
                    name: line.name,
                    coord: this.selectCoordById(line.point[0],ele.point)
                  },
                  {
                    coord: this.selectCoordById(line.point[1],ele.point)
                  }
                ]
              )
            })
            
          }
          series.push({
            name: ele.legend,
            type: "scatter",

            coordinateSystem: 'geo',
            markLine: {
              symbol: ['none','none'],
              label: {
                normal: {
                  show: false
                }
              },
              lineStyle: {
                normal: {
                  type: 'solid'
                }
              },
              data:lineData
            },
            markPoint: {
              symbol: "circle",
              data: pointData
            }
          })
        })
        return series
      },
      addColor() {
        let arr=[];
        this.mapJson.features.forEach( ele => {
          arr.push(
            {
              name: ele.properties.name,
              itemStyle: {
                normal: {
                  color: "#049392"
                },
                emphasis: {
                  color: "#049392"
                }
              }
            }
          )
        })
        return arr;
      },
      pointMoveStateToFalse() {
        if(this.pointMove.state = true && this.pointMove.dataIndex[0] !== -1){
          this.pointMove.state = false
          let point = {
            id: this.key,
            coord: this.data[this.pointMove.dataIndex[0]].point[this.pointMove.dataIndex[1]].coord
          }
          let boo = -1
          for(let i = 0; i < this.pointMoved.length; i++){
            if(this.pointMoved[i].id === point.id){
              boo = i
              break
            }
          }
          if(boo === -1){
            this.pointMoved.push(point)
          }else{
            this.pointMoved[boo] = point
          }
          this.mapDraw()
          this.pointMove.dataIndex[0] = -1
        }
      },
      mapInit() {
        echarts.registerMap('jiangsu', this.mapJson)
        this.chart = echarts.init(document.getElementById('map'))
        this.chart.on('mousedown', params => {
          event.stopImmediatePropagation()
          this.listHide()
          if(params.componentType === "markPoint"){
            if(this.moveSubmit){
              this.moveSubmit = false
              this.moveReady = true
              this.button = [
                {
                  key: "moveSubmit",
                  value: "保存拖动"
                },
                {
                  key: "moveCancel",
                  value: "还原拖动"
                }
              ]
            }
            if(this.moveReady){
              this.key = this.data[params.seriesIndex].point[params.dataIndex].id
              this.pointMove.dataIndex = [params.seriesIndex,params.dataIndex]
              this.pointMove.state = true
            }
          }
        })
        this.chart.on('mouseup', params => {
          this.pointMoveStateToFalse()
        })
        this.chart.on('contextmenu', params =>{
          this.pointMoveStateToFalse()
          event.preventDefault()
          this.listHide()
          if(params.componentType === "markPoint"){
            this.list = this.listData.point
            this.key = this.data[params.seriesIndex].point[params.dataIndex].id
            this.listStyle.left = event.zrX + 1
            this.listStyle.top = event.zrY + 1
            this.listShow = true
          }else if(params.componentType === "markLine"){
            this.list = this.listData.line
            this.key = this.data[params.seriesIndex].line[params.dataIndex].id
            this.listStyle.left = event.zrX + 1
            this.listStyle.top = event.zrY + 1
            this.listShow = true
          }
        })
        this.mapDraw()
      },
      mapDraw() {
        this.chart.setOption({
          title: {
              text: this.title,
              top: 10,
              left: 10,
              textStyle: {
                fontSize: 30,
                color: "#fff"
              }
          },
          legend: {
            show: true,
            left: 20,
            top: "30%",
            orient: "vertical",
            textStyle: {
              color: "#fff"
            },
            data: this.formatLegend(this.data)
          },      
          tooltip: {
              trigger: 'item',
              formatter: function (params) {
                  return params.data.name
              }
          },
          animation: true,
          geo: {
            roam: this.roam,
            map: 'jiangsu',
            zoom: 1.2,
            regions: this.addColor(),
            label:{
                normal: {
                    show: true,
                    textStyle: {
                        color: '#fff'
                    }
                }
            },
            itemStyle: {
              normal: {
                  borderColor: '#172124',
                  borderWidth: 2
              }
            },
          },
          series: this.formatSeries(this.data)
        }); 
      },
      init() {
        window.onresize = function(){
          echarts.dispose(document.getElementById('map'))
          this.$nextTick(function () {
            this.mapInit()
          })
        }.bind(this)
        window.oncontextmenu = function(){
          this.pointMoveStateToFalse()
          event.preventDefault()
          //window.event.returnValue = false; 
        }.bind(this)
        window.onmousemove = function(event){
          window.clearTimeout(window.timeoutID);
          window.timeoutID = setTimeout(() => {
            if(this.pointMove.state){
              this.data[this.pointMove.dataIndex[0]].point[this.pointMove.dataIndex[1]].coord =
              this.chart.convertFromPixel('geo', [event.zrX,event.zrY])
              this.mapDraw()
            }
          }, 200)
          
        }.bind(this)
      }
    },
    mounted() {
      this.$Axios.all([
        this.$Axios.get('/getData').then(res => {
          this.data = res.data
          this.listData = res.listData
          if(res.title){
            this.title = res.title
          }
        }),
        this.$Axios.get('/getMap').then(res => {
          this.mapJson = res
        })
      ]).then( res => {
        this.$nextTick(function () {
          this.mapInit()
        })
        this.init() 
      }).catch( err => {
          console.error(err)
      });
    }
  }
  
</script>
<style scoped>
  #map{
    width: 100%;
    height: 100%;
  }
  #list{
    position:fixed;
    z-index: 999;
    font-size: 80%;
    padding: 5px 0px;
    border-radius:3px;
    background:#eee;
  }
  #list>div:hover{
    cursor:pointer;
    background:#83aaf0;
  }
  #list a{
    padding: 0px 3px;
    color:  #2F4F4F!important;
    text-decoration: none;
  }
  #button {
    position:fixed;
    top:30%;
    right:20px;
    z-index: 999;
    border-radius:3px;
    background: rgb(12,38,51);
    padding: 10px 0px;
    user-select:none;
  }
  #button a{
    padding: 5px 10px;
    background: rgb(19,73,91);
    cursor:pointer;
    color:#fff;
    font-size: 15px;
    line-height: 30px;
    border: 1px solid rgb(12,38,51);
  }
  #button a:hover{
    background: rgb(1,89,95);
  }
  .selected,.selected:hover{
    background: rgb(24,145,143)!important;
  }
  #show{
    position:fixed;
    z-index: 999;
    font-size: 80%;
    padding: 5px 0px;
    border-radius:3px;
    background:#fff;
    width:300px;
    top:20%;
    left:calc(50% - 150px);
    background: linear-gradient(to bottom, rgb(12,38,51) 0px, rgb(12,38,51) 10px, #fff 10px);
  }
  .showTitle{
    background: rgb(12,38,51);
    color: #fff;
    font-size:15px;
  }
</style>


